.prettyHr {
  margin: 0 auto;
  display: block;
  unicode-bidi: -webkit-isolate;
  unicode-bidi: -moz-isolate;
  unicode-bidi: isolate;
  -webkit-margin-before: 0.5em;
          margin-block-start: 0.5em;
  -webkit-margin-after: 0.5em;
          margin-block-end: 0.5em;
  -webkit-margin-start: auto;
     -moz-margin-start: auto;
          margin-inline-start: auto;
  -webkit-margin-end: auto;
     -moz-margin-end: auto;
          margin-inline-end: auto;
  overflow: hidden;
  border-style: inset;
  border-width: 1px;
  max-width: 260px;
  padding: 0;
  border: none;
  /* border-top: medium double #888; */
  border-top: medium double #8b0000;
  color: #888;
  text-align: center;
  overflow: visible;
  z-index: 0;
  position: relative; }
  .prettyHr:after {
    content: '\A7';
    display: inline-block;
    position: absolute;
    color: #8b0000;
    top: -2.2rem;
    left: 50%;
    -webkit-transform: translateX(-50%) rotate(25deg);
       -moz-transform: translateX(-50%) rotate(25deg);
        -ms-transform: translateX(-50%) rotate(25deg);
         -o-transform: translateX(-50%) rotate(25deg);
