.fullWidthContainer {
  width: 100%;
  background: url('/images/menu-outer-bg.gif');
  background-repeat: repeat;
  padding: 1rem 0 1rem 0;
}

.seoulContainer {
  width: 100%;
  margin: 0 auto;
  padding: 0;
  overflow: hidden;
  position: relative;
  @media (min-width: 576px) {
    max-width: 540px;
  }
  @media (min-width: 768px) {
    max-width: 720px;
  }
  @media (min-width: 992px) {
    max-width: 960px;
  }
  @media (min-width: 1460px) {
    max-width: 1430px;
  }
}

.seoulContainer-m {
  background: #fff;
  width: 100%;
  margin: 0 auto;
  padding: 8px;
  @media (min-width: 576px) {
    max-width: 540px;
    padding: 16px;
  }

  @media (min-width: 768px) {
    max-width: 720px;
    padding: 24px;
  }

  @media (min-width: 992px) {
    max-width: 960px;
    padding: 32px;
  }

  @media (min-width: 1200px) {
    max-width: 1140px;
  }
}
